/*General styles*/
.App {
  text-align: center;
  min-height: 100vh;
}

.appContent {
  position:relative;
}

body {
  min-height: 100%;
  background: linear-gradient( #99ccff 20vh, white 100% );
}

/* Hide scrollbar for Chrome, Safari and Opera */
.scrollView::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
.scrollView {
  overflow-y: scroll;
  -ms-overflow-style: none;
}

/*General animations*/
.pulse { 
  animation: pulse 1s infinite;
  animation-timing-function: linear;   
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1);}
  100% { transform: scale(1); }
}

/*Plot Card*/
.removePlot{
  transition: opacity 500ms;
}

.removePlot:hover{
  opacity: 0.4;
}

.actionButton{
  cursor: pointer;
}

/*Plot initializer*/
.plotInitializer {
  transition: all 500ms ease;
}

.plotInitializer.hidden{
  width: 0;
  height: 0;
  overflow: hidden;
}

.btn-flat.initPlotBtn:hover{
  opacity: 0.4;
}

.structurePicker{
  border-radius: 5px;
  border-color: black;
  margin: 10;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  justify-content: space-between;
  background: white;
}

.structureCell {
  cursor: pointer;
  padding: 20px 0px;
  border-radius: 3px;
}

.structureCell.selected{
  background:#bbeeff ;
}

.structureCell:hover:not(.selected){
  background: #99ccff;
}

/*Content tabs*/
.plotTab{
  transition: all 500ms;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  padding:0px 20px;
  border-radius: 20px;
  margin: 0px 20px; 
}

.plotTab:hover {
  background: rgba(255,255,255, 0.4)
}

.plotTab.active{
  background: white;
  color: black;
  cursor: default;
  padding:0px 10px 0px 15px;
}

[contenteditable="true"]{
  cursor: text;
} 

[contenteditable="true"].singleLine {
    white-space: nowrap;
    overflow: hidden;
} 
[contenteditable="true"].singleLine br {
    display:none;

}
[contenteditable="true"].singleLine * {
    display:inline;
    white-space:nowrap;
}

.removeTabBut{
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ccc;
  height: auto;
  cursor: pointer;
  transition: all ease 500ms;
}

.removeTabBut:hover{ 
  transform: scale(1.1);
  color: red
}

.newTabIcon{
  transition: all 500ms;
  cursor: pointer;
}

.newTabIcon:hover{
  color: rgba(0,0,255, 0.4) !important;
  transform: scale(1.1);
}

.plotcard  > .card-content {
  padding: 0
}

/*Settings containers*/

/*Collapsibles*/

.collapsible-header{
  transition: background 1s;
  background: white;
}

.collapsible-header:hover{
  background: #99ccff;
}

.collapsible-header.active{
  background: #77aaff;
  font-weight: bold;
}

.collapsible-body{
  background: white;
}

.fill{
  height: 100%;
  width: 100%
}

/*Switch*/

.switch {
  padding-bottom: 10px;
}

/*Queries*/

.queryCard{
  transition: all 1s;
}

.querySelect  {
  flex: 1
}

/*Structure tags*/
.structTag {
  padding: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: #ccc;
  border-radius: 30px;
  cursor: pointer;
}

.structTag:hover {
  background-color: #c0c0c0;
}

.structTag.active {
  background-color: #999;
}

/*Structure picker*/
.structPickerToggle {
  padding: 5px;
  margin-left: 1px;
  margin-right: 1px;
  background-color: white;
  color: "black";
  border-radius: 5px;
  cursor: pointer;
  transition: all 200ms
}

.structPickerToggle:hover {
  background-color: #c0c0c0;
}

.structPickerToggle.active {
  background-color: #999;
  color: white;
  font-weight: bold;
}

/*Loading tracker*/
.loadingTracker {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 20px;
}

/*Loading chip*/
.loadingChip {
  display: flex;
  flex-direction: row;
  padding: 10px;
  background-color: blanchedalmond;
  border-radius: 5px;
  margin-right: 20px;
  margin-left: 20px;
}